
.colorbutton[data-v-2e7d9ce3] {
    border:1px solid #ddd;
    background: #eeeeee;
    text-indent:0px;
    text-align: center;
    border-radius: 2px;
    margin: 2px;
    padding: 3px;
    display: inline-block;
    cursor: pointer;
    color: #666;
    /* don't allow text selection */
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.colorbutton[data-v-2e7d9ce3]:hover, .button[data-v-2e7d9ce3]:focus {
	foobackground: #ffdddd;
  border: solid 2px #888888;
}
.colorbutton[data-v-2e7d9ce3]:active {
  border: solid 2px #333333;
}
.selected[data-v-2e7d9ce3] {
  border: solid 2px #333333;
}



