/* http://www.cssportal.com/style-input-range/    */
input[type=range][data-v-58a69ebe] {
  height: 19px;
  -webkit-appearance: none;
  margin: 0px 0;
  width: 150px;
  background-color: rgba(0,0,0,0.0);
}
input[type=range][data-v-58a69ebe]:focus {
  outline: none;
}
input[type=range][data-v-58a69ebe]::-webkit-slider-runnable-track {
  width: 100%;
  height: 5px;
  cursor: pointer;
  animate: 0.2s;
  -webkit-box-shadow: 0px 0px 0px #000000;
          box-shadow: 0px 0px 0px #000000;
  background: #E3BCA6;
  border-radius: 1px;
  border: 0px solid #000000;
}
input[type=range][data-v-58a69ebe]::-webkit-slider-thumb {
  -webkit-box-shadow: 0px 0px 0px #000000;
          box-shadow: 0px 0px 0px #000000;
  border: 1px solid #9E4436;
  height: 12px;
  width: 18px;
  border-radius: 25px;
  background: #FF6666;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -4px;
}
input[type=range][data-v-58a69ebe]:focus::-webkit-slider-runnable-track {
  background: #E3BCA6;
}
input[type=range][data-v-58a69ebe]::-moz-range-track {
  width: 100%;
  height: 5px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 0px 0px 0px #000000;
  background: #E3BCA6;
  border-radius: 1px;
  border: 0px solid #000000;
}
input[type=range][data-v-58a69ebe]::-moz-range-thumb {
  box-shadow: 0px 0px 0px #000000;
  border: 1px solid #9E4436;
  height: 12px;
  width: 18px;
  border-radius: 25px;
  background: #FF6666;
  cursor: pointer;
}
input[type=range][data-v-58a69ebe]::-ms-track {
  width: 100%;
  height: 5px;
  cursor: pointer;
  animate: 0.2s;
  background: transparent;
  border-color: transparent;
  color: transparent;
}
input[type=range][data-v-58a69ebe]::-ms-fill-lower {
  background: #E3BCA6;
  border: 0px solid #000000;
  border-radius: 2px;
  box-shadow: 0px 0px 0px #000000;
}
input[type=range][data-v-58a69ebe]::-ms-fill-upper {
  background: #E3BCA6;
  border: 0px solid #000000;
  border-radius: 2px;
  box-shadow: 0px 0px 0px #000000;
}
input[type=range][data-v-58a69ebe]::-ms-thumb {
  margin-top: 1px;
  box-shadow: 0px 0px 0px #000000;
  border: 1px solid #9E4436;
  height: 12px;
  width: 18px;
  border-radius: 25px;
  background: #FF6666;
  cursor: pointer;
}
input[type=range][data-v-58a69ebe]:focus::-ms-fill-lower {
  background: #E3BCA6;
}
input[type=range][data-v-58a69ebe]:focus::-ms-fill-upper {
  background: #E3BCA6;
}



